<template>
  <div class="cb-item">
      <p class="cb-item__title">
          
      </p>
      <p class="cb-item__text">
            " ";       ,
               :    , 
           ,  ,   ,     
          .
      </p>
      <div class="cb-item__line">
          <input id="pic-title" placeholder=" " maxlength="22" type="text" v-model="header" @input="preview.header = header">
          <span> </span>

      </div>
      <div class="cb-item__line">
          <input id="pic-name" placeholder="  " maxlength="36" type="text" v-model="sign" @input="preview.sign = sign">
          <span></span>
      </div>
      <div class="cb-item__line">
          <input id="pic-date" placeholder="29  2015" maxlength="26" type="text" v-model="data" @input="preview.data = data">
          <span> </span>
      </div>
      <div class="cb-item__line--btns">
  <span id="ariston" class="btn-font" :class="{'btn-font--active':preview.painter == 'Ariston'}" @click="preview.painter = 'Ariston'">
  Ariston
  </span>
          <span id="davinci" class="btn-font" :class="{'btn-font--active':preview.painter == 'DaVinci'}" @click="preview.painter = 'DaVinci'">
  DaVinci
  </span>
          <span id="brody" class="btn-font" :class="{'btn-font--active':preview.painter == 'Brody'}" @click="preview.painter = 'Brody'">
  Brody
  </span>
      </div>
  </div>
</template>

<script>
import preview from '../controlers/preview'

export default {
  data: function () {
    return {
      preview: preview,
      header:'',
      sign:'',
      data:''
    }
  }
}
</script>



